<div><div id="controls"><label>size: </label><input id="size" type="number" v-model="size"></div><div id="game"><div class="row" v-for="(row, i) in gameMatrix" id="rowundefined"><div class="tile" v-for="(tile, j) in row" @contextmenu="mark($event, tile)" @click="tilePress(tile)" id="tileundefinedundefined" :style="{height:(tileWidthVMin+'vmin'), lineHeight:(tileWidthVMin+'vmin'), width:(tileWidthVMin+'vmin'), backgroundColor:(tile.hasBomb &amp;&amp; showBombs)?'red':(tile.number==0 &amp;&amp; tile.showN?'white':(tile.showN?'rgb(255, 204, 110)':(tile.marked?'rgb(145, 255, 0)':'')))}">{{tile.showN?tile.number:""}}</div></div></div></div>